<template>
  <div class="upload-wizard enhanced-container">
    <main class="main-content">
      <!--  -->
      <div class="arrow-progress">
        <div v-for="n in 3" :key="n" class="step-container">
          <div :class="['step', step >= n ? 'active' : '']">
            <div class="circle">{{ n }}</div>
            <span class="step-text">{{ getStepText(n) }}</span>
          </div>
          <div v-if="n < 3" :class="['arrow', step >= n+1 ? 'active' : '']"></div>
        </div>
      </div>

      <!-- 1:  +  -->
      <section v-if="step === 1" class="function-card upload-card">
        <div class="card-body">
          <!--  -->
          <p class="notice"></p>
          <!--  -->
          <label class="file-dropzone simple">
            <input type="file" ref="fileInput" @change="onFileChange" class="hidden-input"/>
            <div class="upload-icon"></div>
            <p>{{ file ? file.name : '' }}</p>
          </label>
          <!--  -->
          <p class="tip">
            :
            
          </p>

          <div class="form-group">
            <label> <span class="required">*</span></label>
            <input v-model="form.schemeName" type="text" class="modern-input" placeholder=""/>
          </div>
          <div class="form-group">
            <label> <span class="required">*</span></label>
            <textarea v-model="form.overview" class="modern-input" rows="3" placeholder=""></textarea>
          </div>
          <div class="form-group">
            <label> <span class="required">*</span></label>
            <textarea v-model="form.scoring" class="modern-input" rows="3" placeholder=""></textarea>
          </div>

          <div class="form-group">
            <label></label>
            <div class="kb-picker">
              <label v-for="kb in knowledgeOptions" :key="kb.dirPath" class="kb-chip">
                <input type="checkbox" v-model="selectedKbPaths" :value="kb.dirPath"/>
                <span>{{ kb.fileName || '' }}</span>
              </label>
            </div>
            <div class="kb-actions">
              <router-link to="/knowledgebase" class="kb-btn"></router-link>
            </div>
          </div>

          <div class="action-buttons">
            <button @click="next" :disabled="!isStep1Valid" class="gradient-btn primary"></button>
          </div>
        </div>
      </section>

      <!-- 2:  -->
      <section v-if="step === 2" class="function-card">
        <div class="card-body">
          <p></p>

          <div class="catalog-container">
            <ul class="catalog-list">
              <li v-for="(item, index) in catalogData" :key="index" class="catalog-item">
                <input
                    type="text"
                    v-model="catalogData[index]"
                    class="catalog-input"
                    placeholder=""
                >
                <button class="catalog-btn" @click="addCatalogItem(index)">+</button>
                <button class="catalog-btn" @click="removeCatalogItem(index)" :disabled="catalogData.length <= 1">-</button>
              </li>
            </ul>
          </div>

          <div class="action-buttons">
            <button @click="prev" class="gradient-btn secondary"></button>
            <button @click="next" class="gradient-btn primary" :disabled="isLoading || isLoadingWord">
              <span v-if="!isLoading && !isLoadingWord"></span>
              <span v-else>...</span>
            </button>
          </div>
        </div>
      </section>

      <!-- 3: / -->
      <section v-if="step === 3" class="function-card">
        <div class="card-body">
          <div class="preview-main">
            <div class="editor-header">
              <h2 class="editor-title">{{ form.schemeName || '' }}</h2>
              <div class="editor-tools">
                <button class="tool-btn" @click="downloadGeneratedDoc" :disabled="!fullDocPath">Word</button>
              </div>
            </div>

            <div class="editor-content">
              <div v-if="isLoadingWord" class="loading-indicator">
                <div class="spinner"></div>
                <p>Word...</p>
              </div>

              <!--  mammoth  HTML -->
              <div v-else class="markdown-preview" v-html="safeFullPreviewHtml"></div>
            </div>

            <div v-if="statusMessage" class="status-message" :class="statusType">
              {{ statusMessage }}
            </div>
          </div>

          <div class="action-buttons fixed-height">
            <button @click="prev" class="gradient-btn secondary fixed-size"></button>
            <button @click="downloadGeneratedDoc" class="gradient-btn primary fixed-size" :disabled="!fullDocPath">Word</button>
            <button @click="finish" class="gradient-btn primary fixed-size"></button>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, onUnmounted, watch, nextTick } from 'vue'
import DOMPurify from 'dompurify'
import axios from 'axios'
import mammoth from 'mammoth'

// -------  -------
const isClient = ref(false)
const step = ref(1)
const file = ref(null)
const form = reactive({
  schemeName: '',
  overview: '',
  scoring: ''
})

// string[]
const pendingCatalog = ref(null)
const uploadTitle = ref('')
const gradingRaw = ref('')

// /
const knowledgeOptions = ref([])
const selectedKbPaths = ref([])

onMounted(async () => {
  // 
  try {
    const { data } = await axios.get('/api/KnowledgeBase/getAll')
    if (Array.isArray(data)) {
      knowledgeOptions.value = data
          .map(item => ({
            fileName: item.fileName ?? item.name ?? '',
            dirPath: item.dirPath ?? item.path ?? item.dir ?? ''
          }))
          .filter(kb => kb.dirPath)
    }
  } catch (e) {
    console.warn('', e?.message)
  }
})

//  catalog 
let axiosRespInterceptorId = null
onMounted(() => {
  axiosRespInterceptorId = axios.interceptors.response.use((response) => {
    try {
      const data = response?.data
      if (data && ('catalog' in data)) {
        const list = parseCatalogString(data.catalog)
        if (list.length) pendingCatalog.value = list
      }
    } catch (e) {
      console.warn('catalog :', e)
    }
    return response
  })
  isClient.value = true
})

onUnmounted(() => {
  if (axiosRespInterceptorId !== null) axios.interceptors.response.eject(axiosRespInterceptorId)
})

// -------  -------
const catalogData = ref([])

//  -> 
watch(step, (now, prev) => {
  if (prev === 1 && now === 2 && pendingCatalog.value?.length) {
    applyCatalogList(pendingCatalog.value)
    pendingCatalog.value = null
    showStatusMessage('', 'success')
  }
})

function parseCatalogString(catalogStr) {
  if (Array.isArray(catalogStr)) return catalogStr.filter(Boolean).map(s => String(s).trim())
  if (typeof catalogStr !== 'string') return []
  return catalogStr.split(/\r?\n/).map(s => s.trim()).filter(Boolean)
}

function applyCatalogList(list) {
  if (!Array.isArray(list) || !list.length) return
  catalogData.value = list
}

// 
function addCatalogItem(index) {
  catalogData.value.splice(index + 1, 0, ` ${catalogData.value.length + 1}`)
}
function removeCatalogItem(index) {
  if (catalogData.value.length <= 1) return
  catalogData.value.splice(index, 1)
}

// -------  -------
async function onFileChange(e) {
  const f = e.target.files[0]
  if (!f) return
  file.value = f

  const formData = new FormData()
  formData.append('file', f)

  try {
    const response = await axios.post('api/BidGeneration/uploadTl', formData, {
      headers: { 'Content-Type': 'multipart/form-data' }
    })
    const data = response.data

    uploadTitle.value = data.title || ''
    form.schemeName = data.title || ''
    form.overview = data.overview || ''
    form.scoring = data.grading || ''
    gradingRaw.value = typeof data.grading === 'string' ? data.grading : JSON.stringify(data.grading || '')

    if (data.catalog) {
      const list = parseCatalogString(data.catalog)
      if (list.length) {
        pendingCatalog.value = list
        showStatusMessage('', 'success')
      }
    }
  } catch (err) {
    console.error('', err.response?.data?.message || err.message)
    showStatusMessage('' + (err.response?.data?.message || err.message), 'error')
  }
}

// -------  +  -------
const isLoading = ref(false)
const isLoadingWord = ref(false)
const statusMessage = ref('')
const statusType = ref('')

//  docx 
const fullDocPath = ref('')
//  HTML mammoth 
const fullPreviewHtml = ref('')

//  prompt
function buildFullPrompt() {
  const parts = []
  if (form.schemeName) parts.push(`${form.schemeName}`)
  if (form.overview)   parts.push(`\n${form.overview}`)
  if (form.scoring)    parts.push(`\n${form.scoring}`)
  return parts.join('\n\n')
}

// -------  +  -------
async function generateFullDocument() {
  if (!catalogData.value?.length) {
    showStatusMessage('', 'warning')
    return false
  }

  const payload = {
    prompt: buildFullPrompt(),
    catalog: catalogData.value.slice(),
    knowledgeBaseName: selectedKbPaths.value.slice()
  }

  try {
    isLoading.value = true
    showStatusMessage('...', 'info')

    // 
    const { data } = await axios.post('/api/BidGeneration/generateFull', payload, {
      headers: { 'Content-Type': 'application/json' }
    })

    // 
    const docxPath = data?.docx_path || data?.docxPath || data?.response
    if (!docxPath || typeof docxPath !== 'string') {
      showStatusMessage(' docx_path', 'error')
      return false
    }

    fullDocPath.value = docxPath

    //  Word  HTML 
    const html = await loadWordFileAndConvert(docxPath)
    if (html) {
      fullPreviewHtml.value = html
      showStatusMessage('', 'success')
      return true
    } else {
      showStatusMessage('Word ', 'warning')
      return false
    }
  } catch (err) {
    console.error('', err)
    showStatusMessage('' + (err.response?.data?.message || err.message), 'error')
    return false
  } finally {
    isLoading.value = false
  }
}


//  Word
function downloadGeneratedDoc() {
  if (!fullDocPath.value) {
    showStatusMessage(' Word ', 'warning')
    return
  }
  const a = document.createElement('a')
  a.href = downloadUrl(fullDocPath.value)
  a.target = '_blank'
  document.body.appendChild(a)
  a.click()
  a.remove()
}

//  HTML
const safeFullPreviewHtml = computed(() => DOMPurify.sanitize(fullPreviewHtml.value || ''))

// URL 
function downloadUrl(filePath) {
  return `/api/materials/download?filePath=${encodeURIComponent(filePath)}`
}

// Word -> HTML
async function loadWordFileAndConvert(filePath) {
  try {
    if (!filePath || typeof filePath !== 'string') {
      showStatusMessage('', 'error')
      return null
    }
    showStatusMessage('Word...', 'info')

    const response = await fetch(downloadUrl(filePath))
    if (!response.ok) throw new Error(`: ${response.status} ${response.statusText}`)

    const blob = await response.blob()
    if (!blob || blob.size === 0) {
      showStatusMessage('', 'error')
      return null
    }

    const validTypes = [
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
      'application/msword',
      'application/octet-stream'
    ]
    if (!validTypes.includes(blob.type)) {
      showStatusMessage(`: ${blob.type}`, 'warning')
    }

    isLoadingWord.value = true
    showStatusMessage('Word...', 'info')

    const arrayBuffer = await blob.arrayBuffer()

    //   HTML 
    const result = await mammoth.convertToHtml({ arrayBuffer }, {
      convertImage: mammoth.images.imgElement((image) =>
          image.readAsDataURL().then((dataUrl) => ({ src: dataUrl }))
      )
    })

    if (result.messages?.length) {
      const errors = result.messages.filter(m => m.type === 'error')
      if (errors.length) showStatusMessage(` ${errors.length} `, 'error')
    }

    isLoadingWord.value = false
    return result.value || '<p></p>'
  } catch (err) {
    console.error('Word', err)
    showStatusMessage('Word ' + err.message, 'error')
    isLoadingWord.value = false
    return null
  }
}

// -------  -------
async function next() {
  if (isLoading.value || isLoadingWord.value) return

  if (step.value === 1) {
    step.value = 2
    return
  }

  if (step.value === 2) {
    const ok = await generateFullDocument()
    if (ok) {
      step.value = 3
    }
  }
}

function prev() {
  if (step.value === 2) {
    step.value = 1
  } else if (step.value === 3) {
    step.value = 2
  }
}

function finish() {
  showStatusMessage('/', 'success')
  setTimeout(() => {
    step.value = 1
  }, 1500)
}

// -------  -------
const isStep1Valid = computed(() => file.value && form.schemeName && form.overview && form.scoring)

const getStepText = (n) => ['', '', ''][n - 1]

function showStatusMessage(message, type) {
  statusMessage.value = message
  statusType.value = type
  setTimeout(() => {
    statusMessage.value = ''
    statusType.value = ''
  }, 3000)
}
</script>

